.checkout{
    display: flex;
    padding: 20px;
    background-color: white;
    height: max-content;
}
.checkout_ad{
    width: 100%;
    margin-bottom: 10px;

}
.checkout_title{
    margin-right: 10px;
    padding: 10px;
    border-bottom: 1px solid lightgray;
}
@media (max-width: 768px) {
    .checkout{
        flex-direction: column;
       align-items: center;
        justify-items: center;

    }

}
  

